import React, { Component } from 'react';
import Input from './Input';

class QuizForm extends Component {

  render() {
    return (
      <div className="QuizForm">
        <h4 className="ui header">{this.props.header}</h4>
        <Input label="" reference={this.props.reference} target={this.props.target} id="title" placeholder="" name="title" default={this.props.data.title} />
        <Input label="" reference={this.props.reference} target={this.props.target} id="description" placeholder="" name="description" default={this.props.data.description} />
        <div className="two fields">
          <Input label="ID" reference={this.props.reference} target={this.props.target} id="id" placeholder="" name="id" default={this.props.data.id} />
          <Input label="" reference={this.props.reference} target={this.props.target} id="target" placeholder="" name="target" default={this.props.data.target} />
        </div>
      </div>
    );
  }

}

export default QuizForm;



