import React, { Component } from 'react';
import Input from './Input';
import './OptionForm.css';

class OptionForm extends Component {

  render() {

    return (
      <div className="OptionForm" >
        <h4 className="ui header">{this.props.header}</h4>
        <a className="ui right top floated orange icon labeled mini button" onClick={this.props.onDelete} data-optionid={this.props.id} >
         <i className="icon trash" />
         
        </a>
        <Input label="" reference={this.props.reference} target={this.props.target} id={this.props.id} placeholder="" name="title" default={this.props.item.title} />
        <div className="two fields">
          <Input label="" reference={this.props.reference} target={this.props.target} id={this.props.id} placeholder="" name="id" default={this.props.item.id} />
          <Input label="" reference={this.props.reference} target={this.props.target} id={this.props.id} placeholder="" name="value" default={this.props.item.value} />
        </div>
        <hr className="ui divider" />
      </div>
    );

  }

}

export default OptionForm;



